import { useFormContext } from 'react-hook-form';
import { FormField, FormItem, FormLabel, FormControl, FormMessage } from '@/components/ui/form';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';

interface Props {
    className?: string;
    name: string;
    label?: string;
    placeholder?: string;
    options?: {
        name: string;
        value: string;
    }[];
    error?: string | null;
    data?: any;
    disabled?: boolean;
}

export default function SelectInputForm({
    className,
    name,
    label,
    options,
    placeholder,
    error,
    data,
    disabled,
}: Props) {
    const { control } = useFormContext();

    return (
        <FormField
            control={control}
            name={name}
            render={({ field }) => (
                <FormItem className={className}>
                    <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                        {label}
                    </FormLabel>
                    <Select
                        onValueChange={field.onChange}
                        defaultValue={field.value}
                        value={data && String(field.value)}
                        disabled={disabled}
                    >
                        <FormControl>
                            <SelectTrigger>
                                <SelectValue placeholder={<span style={{ color: '#D1D1D1' }}>{placeholder}</span>} />
                            </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                            {options?.map(({ name, value }) => (
                                <SelectItem key={value} value={value} className="font-cairo">
                                    {name}
                                </SelectItem>
                            ))}
                        </SelectContent>
                    </Select>
                    <FormMessage>{error}</FormMessage>
                </FormItem>
            )}
        />
    );
}
